import './App.css';
import './components/login.css';
import React from 'react';
import {Button} from 'reactstrap';
import {HashRouter as Router, Switch, Route, Link, Redirect} from 'react-router-dom';
import FbLogin from './components/fbLogin';
import Login from './components/login';
import Logout from './components/logout';

class App extends React.Component {
  state = {
    loggedIn: false,
    accessToken: '',
  }
  redirectPage = (value) => {
    this.setState({
      loggedIn: value
    })
  }
  setAccess = (number) => {
    this.setState({
      accessToken: number
    })
  }
  render() {
    const {loggedIn, accessToken} = this.state;
    const PrivateRoute = ({component: Component, ...rest}) => (
      <Route {...rest} render={(props) => {
        //This will need to be document.cookie - accesstoken
        if (accessToken.length > 2) {
          return <Component {...props} />
        } else {
          return <Redirect to={{pathname: '/', state: {from: props.location} }} />
        }
      }}/>  
    )
      return (
        <Router basename='/'>
          <Switch>
                <Route exact path="/">
                  
                    <div className="form">
                    <div className="rowMine">
                      <Login showBtn={this.showBtn} />
                    </div>
                    </div>
                  
                </Route>       
                <Route path="/fb">
               
                    <div className="form">
                    <div className="rowMine">
                      <FbLogin loggedIn={loggedIn} redirectPage={this.redirectPage} setAccess={this.setAccess} accessToken={accessToken} />
                    </div>
                    </div>
                  
                </Route>
                
                <PrivateRoute component={Logout} path='/dashboard' redirectPage={this.redirectPage} accessToken={accessToken} />
          </Switch>
        </Router>
      )
  }
}

export default App;
